<template>
  <p-container class="output-container">
    <div class="content" ref="output" v-html="content">
    </div>
    <div class="tools">
      <button class="copy" @click="copy">
        <svg-icon iconName="copy" />
      </button>
    </div>
  </p-container>
</template>

<script>
import { ref } from 'vue'

import PContainer from '@/components/common/Container/index.vue'

export default {
  name: 'OutputContainer',
  components: {
    PContainer
  },
  props: {
    content: String
  },

  setup() {
    const output = ref()

    // 复制
    const copy = () => {
      let createRange = document.createRange()
      createRange.selectNodeContents(output.value)
      const selection = document.getSelection()
      selection.removeAllRanges()
      selection.addRange(createRange)
      document.execCommand('Copy')
      selection.removeAllRanges()
    }

    return {
      output,
      copy
    }
  }
}
</script>

