<template>
  <top-menu @convert="convert"></top-menu>
  <div class="translator">
    <input-container ref="input"></input-container>
    <output-container :content="output"></output-container>
  </div>
</template>

<script>
import { ref } from 'vue'

import { getTranslation, getRomanize } from '@/utils/utils.js'

// @ is an alias to /src
import TopMenu from '@/components/TopMenu.vue'
import InputContainer from '@/components/InputContainer.vue'
import OutputContainer from '@/components/OutputContainer.vue'

export default {
  name: 'Home',
  components: {
    TopMenu,
    InputContainer,
    OutputContainer
  },

  setup() {
    const input = ref()
    const output = ref('')

    const convert = (val) => {
      if (input.value.getConvert() == '') {
        return
      }

      let retain = input.value.getRetain().split('\n')
      let convert = input.value.getConvert().split('\n')

      let query_rm = ''
      let query_tran = ''

      for (var i = 0; i < convert.length; i++) {
        query_rm = query_rm + convert[i] + '\n'
        query_tran = query_tran + encodeURIComponent(convert[i]) + '%0A'
      }

      let tlResult
      let rmResult
      getTranslation(query_tran).then((response) => {
        tlResult = response
        getRomanize(query_rm).then((response) => {
          rmResult = response
          print(val.value)
        })
      })

      function print(setting) {
        let ret = ''
        for (var i = 0; i < retain.length; i++) {
          if (retain[i] != '') {
            ret = ret + '<div class="retain-text">' + retain[i] + '</div>'
          } else {
            ret = ret + '<br/>'
          }
        }
        let para = ''
        for (var i = 0; i < tlResult.length; i++) {
          if (convert[i] != '') {
            para =
              para +
              (setting.includes('Japanese')
                ? '<div class="j-text">' + convert[i] + '</div>'
                : '') +
              (setting.includes('Romaji')
                ? '<div class="r-text">' + rmResult[i] + '</div>'
                : '') +
              (setting.includes('Chinese')
                ? '<div class="c-text">' + tlResult[i][0].tgt + '</div>'
                : '') +
              '<br/>'
          } else {
            para = para + '<br/>'
          }
        }
        output.value =
          (ret == '<br/>' ? '' : ret + '<br/>' + '<br/>' + '<br/>') + para
      }
    }

    return {
      input,
      output,
      convert
    }
  }
}
</script>

<style lang="scss" scoped>
.translator {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
}
</style>