<template>
  <p-container class="input-container">

    <div class="input-area">
      <p-input ref="retainText" class="topic" placeholder="RETAIN">
      </p-input>
      <div class="split"></div>
      <p-input ref="convertText" class="content" placeholder="CONVERT"></p-input>
    </div>

    <div class="tools">
      <button class="clear" @click="clearInput">
        <svg-icon iconName="x" />
      </button>
    </div>
  </p-container>
</template>

<script>
import { defineComponent, ref } from 'vue'

import PContainer from '@/components/common/Container/index.vue'
import PInput from '@/components/common/Input/index.vue'

export default defineComponent({
  name: 'InputContainer',
  components: {
    PContainer,
    PInput
  },

  setup() {
    const retainText = ref()
    const convertText = ref()

    function getRetain() {
      return retainText.value.getContent()
    }

    function getConvert() {
      return convertText.value.getContent()
    }

    function clearInput() {
        retainText.value.clear()
        convertText.value.clear()
    }

    return {
        retainText,
        convertText,
        getRetain,
        getConvert,
        clearInput
    }
  }
})
</script>

