<template>
  <svg class="svg-class" aria-hidden="true">
    <use :href="'#icon-'+iconName" />
  </svg>
</template>

<script>
export default {
  name: 'SvgIcon',
  props: {
    iconName: {
      type: String,
      default: '',
      require: true
    }
  },
  setup() {
    //获取当前svg目录下的所有svg文件
    const req = require.context('@/assets/icons', false, /\.svg$/)
    //解析
    const requireAll = (requireContext) => {
      return requireContext.keys().map(requireContext)
    }
    requireAll(req)
  }
}
</script>

<style scoped>
.svg-class {
  width: 1em;
  height: 1em;
  fill: currentColor;
  overflow: hidden;
}
</style>