<template>
  <basic></basic>
</template>

<script>
import { useStore } from 'vuex'

// @ is an alias to /src
import Basic from '@/components/layout/Basic.vue'
import { onMounted } from '@vue/runtime-core'

export default {
  name: 'App',
  components: {
    Basic
  },

  setup() {
    const $store = useStore()

    function checkSize() {
      var clientWidth = document.body.clientWidth
      if (clientWidth < 500) {
        $store.dispatch('SetMobile', true)
      } else {
        $store.dispatch('SetMobile', false)
      }
    }

    window.onresize = () => {
      checkSize()
    }

    onMounted(() => {
      checkSize()
    })
  }
}
</script>

