<template>
  <transition name="overlay">
    <div class="overlay" v-if="show && overlay"></div>
  </transition>
  <transition name="panel">
    <div class="panel" v-if="show">
      <p-container class="panel-con">
        <div class="panel-header">
          <slot name="header"></slot>
          <button class="clear" @click="close">
            <svg-icon iconName="x" />
          </button>
        </div>
        <slot name="body"></slot>
      </p-container>
    </div>
  </transition>
</template>

<script>
import { computed } from 'vue'

import PContainer from '@/components/common/Container/index.vue'

export default {
  name: 'Panel',
  components: {
    PContainer
  },
  props: {
    show: {
      type: Boolean,
      default: () => false
    },
    overlay: {
      type: Boolean,
      default: () => false
    }
  },
  emits: ['update:show'],

  setup(props, { emit }) {
    const overlay = computed(() => props.overlay)

    const show = computed({
      get: () => props.show,
      set: (value) => {
        emit('update:show', value)
      }
    })

    const close = () => {
      // 直接改变值
      show.value = !show.value
    }

    return {
      overlay,
      show,
      close
    }
  }
}
</script>

<style scoped lang="scss">
// overlay動畫
// 进入 & 退出
.overlay-enter-from,
.overlay-leave-to {
  opacity: 0;
}
// 进入的这个阶段 & 退出的这个阶段
.overlay-enter-active,
.overlay-leave-active {
  transition: all 0.2s ease;
}
// 进入后 & 退出后
.overlay-enter-to,
.overlay-leave-from {
  opacity: 1;
}

// panel動畫
// 进入 & 退出
.panel-enter-from,
.panel-leave-to {
  transform: translateY(100%);
}
// 进入的这个阶段 & 退出的这个阶段
.panel-enter-active,
.panel-leave-active {
  transition: all 0.2s ease;
}
// 进入后 & 退出后
.panel-enter-to,
.panel-leave-from {
  transform: translateY(0%);
}

.overlay {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
}

.panel {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  justify-content: center;
  align-items: flex-end;
}

.panel-con {
  display: flex;
  min-height: 90%;
  flex-direction: column;
  background-color: white;
  overflow: hidden;
  border-radius: 10px 10px 0 0;
  border: 0;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #dd005c;
  color: white;
  padding: 0 0 0 20px;
}

.clear {
  font-size: 28px;
  color: white;
  padding: 5px;
  margin: 5px;
}
</style>
