<template>
  <div class="check-box-group">
    <slot></slot>
  </div>
</template>

<script>
import { toRefs, computed, provide, nextTick } from 'vue'

export default {
  name: 'CheckBoxGroup',
  props: {
    modelValue: {
      type: [Array],
      default: () => undefined
    }
  },
  emits: ['change', 'update:modelValue'],
  setup(props, context) {
    // 定义事件
    const changeEvent = (value) => {
      context.emit('update:modelValue', value)
      nextTick(() => {
        context.emit('change', value)
      })
    }

    const modelValue = computed({
      get() {
        return props.modelValue
      },
      set(val) {
        changeEvent(val)
      }
    })

    // 向子组件传递
    provide('CheckBoxGroup', {
      name: 'CheckBoxGroup',
      modelValue,
      ...toRefs(props),
      changeEvent
    })
  }
}
</script>

<style lang="scss" scoped>
.check-box-group {
  display: flex;
}
</style>