<template>
  <label>
    <input type="checkbox" v-model="model" :value="label" :checked="isChecked"
      @change="onChange" />
    <div class="con" :class="{'button':isButton}">
      <slot></slot>
      <svg-icon iconName="check" class="check" v-if="!isButton && isChecked" />
    </div>
  </label>
</template>

<script>
import { computed, inject } from 'vue'

export default {
  name: 'CheckBox',
  props: {
    modelValue: {
      type: [Boolean, Number, String],
      default: () => undefined
    },
    label: {
      type: String,
      default: () => ''
    },
    isButton: {
      type: Boolean,
      default: () => false
    }
  },
  emits: ['change', 'update:modelValue'],

  setup(props, { emit }) {
    // CheckBoxGroup provide後子組件用inject獲取CheckBoxGroup，獲取不到則為空
    const checkboxGroup = inject('CheckBoxGroup', {})

    const label = computed(() => props.label)
    const isButton = computed(() => props.isButton)

    // 判断是否为多选框组
    const isGroup = computed(
      () => checkboxGroup && checkboxGroup.name === 'CheckBoxGroup'
    )

    // modelValue = model, 且每次modelValue更新時都會執行model的get和set
    const model = computed({
      get() {
        // 獲取model時，先判斷是不是Group內，是的話輸出Group的數據
        // (即每個checkBox內都存着checkBoxGroup的數據)
        return isGroup.value ? checkboxGroup.modelValue.value : props.modelValue
      },
      set(val) {
        if (isGroup.value) {
          // 如果是Group的話調用Group里的changeEvent
          checkboxGroup.changeEvent(val)
        } else {
          // 不是的話，更新自己的modelValue
          emit('update:modelValue', val)
        }
      }
    })

    // 獲取model數據，判断多选框是否选中
    const isChecked = computed(() => {
      const value = model.value // 如果是Group的數據格式會是Array
      if (typeof value === 'boolean') {
        return value
      } else if (typeof value === 'object') {
        return value.includes(props.label)
      }
      return null
    })

    // 不是Group時可用@change獲取checkBox的狀態
    const onChange = (e) => {
      const target = e.target
      const value = target.checked ? true : false
      emit('change', value, e)
    }

    return {
      model,
      label,
      isButton,
      isChecked,
      onChange
    }
  }
}
</script>

<style lang="scss" scoped>
input {
  display: none;
}

label {
  display: flex;
  flex: 1 1 auto;
}

.con {
  cursor: pointer;
  display: flex;
  flex: 1 1 auto;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #c6c6c6;
  padding: 15px 15px 15px 15px;
  color: #494949;
  -webkit-user-select: none;
  background-color: white;
  font-size: 16px;
}

.button {
  border-radius: 0;
  border: 1px solid #c6c6c6;
  justify-content: center;
  padding: 10px;
}

label:hover .button {
  background-color: #ededed;
}

input:checked + .button {
  background-color: #dd005c;
  color: white;
  border-color: white;
}

.check {
  color: #dd005c;
  font-size: 20px;
}

label:first-child .button {
  border-radius: 10px 0 0 10px;
}

label:not(:last-child) .button {
  border-right: 0;
}

label:last-child .button {
  border-radius: 0 10px 10px 0;
}
</style>