<template>
  <div ref="inputText" class="input" contenteditable="true"
    :style="{'--content': content}">
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'Input',
  props: {
    placeholder: String
  },

  setup(props) {
    const inputText = ref()
    const content = '"' + props.placeholder + '"'

    function getContent() {
      let text = inputText.value.innerText
      return text
    }

    function clear(){
        inputText.value.innerText = ""
    }

    return {
      inputText,
      content,
      getContent,
      clear
    }
  }
}
</script>

<style lang="scss" scoped>
.input {
  display: flex;
  flex: 1 1 auto;
  border: none;
  outline: 0;
  resize: none;
  /*这两行代码可以解决大部分场景下的换行问题*/
  word-break: break-all;
  word-wrap: break-word;
  /*但在有些场景中，还需要加上下面这行代码*/
  white-space: normal;
  border-radius: 0px;
  overflow-y: auto;
  -webkit-user-modify: read-write-plaintext-only; // 内容只可输入纯文本
  padding: 20px;
}

.input:empty::before {
  content: var(--content);
  color: #aaaaaa;
}
</style>