import X2JS from 'x2js'
import ConvertApi from '@/api/conversion.js'

/**
 * 獲取翻譯結果
 * @param {string} query 要翻譯的內容
 * @returns {json} 翻譯結果
 */
export function getTranslation(query) {
    return new Promise((resolve, reject) => {
        ConvertApi.translate(query).then((response) => {
            resolve(response.translateResult)
        })
    })
}

/**
 * 獲取羅馬音
 * @param {string} query 要轉換的內容
 * @returns {Array} 轉換結果
 */
export function getRomanize(query) {
    var x2js = new X2JS()
    return new Promise((resolve, reject) => {
        let result = new Array()
        ConvertApi.romanize(query).then((response) => {
            let jsonObj = x2js.xml2js(response) //xml为需要转换的字符串xml
            let data
            try {
                data = jsonObj.ul.li
            } catch (err) {
                resolve()
            }
            for (var i = 0; i < data.length; i++) {
                let line = ''
                if (data[i] != '') {
                    for (var j = 0; j < data[i].span.length; j++) {
                        let word = data[i].span[j]._title
                        if (word) {
                            line = line + word + ' '
                        } else {
                            line = line + '  '
                        }
                    }
                    result.push(line)
                } else {
                    result.push('\n')
                }
            }
            resolve(result)
        })
    })
}
