<template>
  <p-container class="top-menu">
    <div class="setting" v-if="!mobile">
      <div class="option">OPTIONS</div>
      <p-check-box-group v-model="selected">
        <!-- :class="{ active: isActive }" -->
        <p-check-box isButton v-for="(option, index) in options" :key="index"
          :label="option">
          {{ option }}</p-check-box>
      </p-check-box-group>
    </div>

    <div class="setting mobile" v-if="mobile">
      <button @click="showOptions = true">
        <div>OPTIONS</div>
        <svg-icon iconName="down" />
      </button>
    </div>
    <button @click="onConvert">CONVERT</button>
  </p-container>

  <p-panel overlay v-model:show="showOptions" v-if="mobile">
    <template #header>
      <div class="option">OPTIONS</div>
    </template>
    <template #body>
      <p-check-box-group v-model="selected" style="flex-direction: column;">
        <p-check-box v-for="(option, index) in options" :key="index"
          :label="option">
          {{ option }}</p-check-box>
      </p-check-box-group>
    </template>
  </p-panel>
</template>

<script>
import { ref } from 'vue'

import { useGetters } from '@/utils/useMapper.js'

import PContainer from '@/components/common/Container/index.vue'
import PCheckBox from '@/components/common/CheckBox/index.vue'
import PCheckBoxGroup from '@/components/common/CheckBoxGroup/index.vue'

import PPanel from '@/components/common/Panel/index.vue'

export default {
  name: 'TopMenu',
  components: {
    PContainer,
    PCheckBox,
    PCheckBoxGroup,
    PPanel
  },
  emits: {
    convert: null
  },

  setup(props, context) {
    const storeGetters = useGetters(['mobile'])

    const selected = ref(['Japanese', 'Romaji', 'Chinese'])
    const options = ref(['Japanese', 'Romaji', 'Chinese'])

    const showOptions = ref(false)

    const onConvert = () => {
      // 抛出事件
      context.emit('convert', selected)
    }

    return {
      ...storeGetters,
      selected,
      options,
      showOptions,
      onConvert
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.top-menu {
  display: flex;
  min-height: 50px;
  border-radius: 10px 10px 0 0;
  border-bottom: 0;
  align-items: center;
  justify-content: space-between;
  padding: 0px 10px;
  flex-wrap: wrap;
}

.setting {
  display: flex;
  align-items: center;
  margin-left: 10px;
  flex-wrap: wrap;
}

.option {
  font-weight: bold;
  padding-right: 10px;
}

button {
  padding: 10px;
  background-color: #dd005c;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  margin-left: 10px;
}

button:hover {
  background-color: #f03e88;
}

button:active {
  background-color: #dd005c;
}

.mobile {
  flex: 1 1 auto;
  margin: 0;
}

.mobile button {
  background-color: white;
  margin-left: 0px;
  color: #494949;
  justify-content: flex-start;

  div {
    margin-right: 5px;
  }
}

.mobile button:hover {
  color: #f03e88;
}

.mobile button:active {
  color: #f03e88;
}
</style>
