import request from '@/utils/request.js'
import qs from 'qs'

export default {
    /**
     * 日语转罗马音
     * @param {string} query
     * @returns {xml} 每个词的罗马音
     */
    romanize: function (query) {
        // 以form-data的形式發送
        let formData = new FormData()
        formData.append('mode', 'japanese')
        formData.append('q', query)
        return request({
            url: '/kw/works/ajax/romanize/romanize.cgi',
            method: 'post',
            data: formData,
            headers: {
                'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundaryn8D9asOnAnEU4Js0'
            }
        })
    },

    /**
     * 日语转中文
     * @param {string} query
     * @returns {json} 翻譯結果
     */
    translate: function (query) {
        return request({
            url: '/fy/translate?&doctype=json&type=AUTO&version=2.1&i=' + query
        })
    }
}
